exports.default = {
    data: function data() {
        return {
            link: [],
            breadItemLength: 0
        };
    },
    created: function created() {
        this.link = this.$route.matched;
        //        console.log(this.$route.matched)
        // 获取当期路由链的长度  例子:home/detail/01  此路由链的长度为3
        var length = this.$route.matched.length;
        // 正则匹配以/开头和结尾的任意路由 例 /home/
        var reg = /^\/.*\/$/g;
        // 正则匹配最后一级路由, 如果最后一级路由是/01/这样子就返回ture
        var showIcon = reg.test(this.$route.matched[length - 1].path);
        // 只有标题大于3级才显示返回按钮
        if (showIcon) {
            this.breadItemLength = length - 1;
        } else {
            this.breadItemLength = length;
        }
    },

    methods: {
        jump: function jump() {
            var aa = this.link[this.breadItemLength - 2];
            if (!aa) return false;
            if (this.activeTab && this.pactNo && this.userNo && this.pactSubFlag) {
                this.$router.push({
                    name: 'detail_contract',
                    params: {
                        activeTab: this.activeTab,
                        pactNo: this.pactNo,
                        userNo: this.userNo
                    }
                });
            } else if (this.activeTab && this.pactNo) {
                this.$router.push({
                    name: aa,
                    params: {
                        activeTab: this.activeTab,
                        pactNo: this.pactNo
                    }
                });
            } else if (this.userNo) {
                console.log("走我的判断了");
                console.log(aa);
                this.$router.push(aa);
            } else {
                //                this.$router.push(aa)
                this.$router.go(-1);
            }
        }
    },

    props: {
        yiji: {
            type: String,
            default: ''
        },
        erji: {
            type: String,
            default: ''
        },
        sanji: {
            type: String,
            default: ''
        },
        siji: {
            type: String,
            default: ''
        },
        wuji: {
            type: String,
            default: ''
        },
        liuji: {
            type: String,
            default: ''
        },
        activeTab: {
            type: String,
            default: ''
        },
        pactNo: {
            type: String,
            default: ''
        },
        userNo: {
            type: String,
            default: ''
        },
        pactSubFlag: {
            type: String,
            default: ''
        }
    }
};

